.featured {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItem {
    flex: 1;
    margin: 10px 20px;
    padding: 30px;
    border-radius: 10px;
    cursor: pointer;
    -webkit-box-shadow: 1px 1px 4px 4px #787878;
    box-shadow: 1px 1px 4px 4px #787878;
}

.featuredTitle{
    font-size: 20px;
}

.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}

.featuredMoneyRate{
    display: flex;
    align-items: center;
    margin-left: 10px;
}

.featuredIcon.positive{
    font-size: 14px;
    margin-left: 5px;
    color: green;
}

.featuredIcon.negetive{
    color: red;
    font-size: 14px;
    margin-left: 5px;
}

.featuredSub{
    font-size: 15px;
    color: gray;
}