.sidebar{
    flex: 1;
    height: calc(100vh - 50px);
    background-color: #ececf4;
    position: sticky;
    top: 50px;
    font-size: clamp(12px, 1vw, 16px);
}

.sidebarWrapper{
    padding: 20px;
    color: #555;
}

.sidebarMenu{
    margin-bottom: 10px;
}

.sidebarTitle{
    font-size: 13px;
    color: rgb(191, 188, 188);
}

.sidebarList{
    list-style: none;
    padding: 5px;
}

.sidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

.sidebarListItem.active, .sidebarListItem:hover{
    background-color:  #cdcadd;
}

.sidebarIcon{
    margin-right: 5px;
    font-size: clamp(17px, 2vw, 20px) !important;
}

.hamburger {
    display: none;
}